.product-container {
  background-color: #ffffff;
  min-height: 80vh;
  margin: 2em 0;
  padding: 3em;
}

.product-container.row > * {
  padding: 0;
}

.darker-text {
  color: #333;
}

.product-main .flickity-viewport {
  max-height: 500px !important;
  touch-action: pan-y;
}

.carousel {
  background: #fafafa;
  margin-bottom: 40px;
  width: 500px;
  //   height: 100%;
}

.product-main .carousel-cell {
  width: 100%;
  max-height: 500px;
  margin-right: 10px;
  counter-increment: carousel-cell;
  //   background: #8c8;
}

/* cell number */
.carousel-cell:before {
  display: block;
  text-align: center;
  //   content: counter(carousel-cell);
  //   line-height: 200px;
  //   font-size: 80px;
  //   color: white;
}

.carousel-nav .carousel-cell {
  height: 100px;
  width: 100px;
  max-width: 113px;
}

.carousel-nav {
  background: none;
}

.rating {
  font-size: 2em;
}

.rating-colour {
  color: #333;
}

.rating-colour-dim {
  color: #ccc;
}

.product-title {
  font-size: 1.6em;
  color: #000000;
  font-weight: 500;
}

.product-price {
  font-size: 2.3em;
  color: coral;
  font-weight: 700;
}

.custom-select {
  display: inline-block;
  width: 100%;
  height: calc(1.5em + 1.75rem + 2px);
  padding: 0.875rem 3.5rem 0.875rem 1.5rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #111;
  vertical-align: middle;
  background: #fff
    url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3e%3cpolyline fill='none' stroke='%23525252' stroke-width='2' stroke-linecap='round' stroke-linejoin='round' points='6 9 12 15 18 9'/%3e%3c/svg%3e")
    no-repeat right 1.5rem center/1rem 1rem;
  border: 1px solid #e5e5e5;
  border-radius: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.add-to-cart {
  color: #ffffff;
  font-size: 1.4em;
  font-weight: 300;
}

.add-to-cart svg,
.wishlist-button svg {
  margin-left: 1em;
  height: 50px;
}

.wishlist-button {
  color: #333;
  font-size: 1.4em;
  font-weight: 300;
}

